@import '../constants/themes';
@import '../constants/colors';

.timer {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;

    &__header {
        font-size: 32px; }

    &__time {
        display: flex;
        justify-content: center;
        align-items: center;
        font-weight: bold;
        font-size: 40px;
        height: 300px;
        width: 300px;
        padding: 10px;
        border-radius: 100%;
        position: relative;
        transition: .3s ease-in-out color;

        &::after {
            content: '';
            position: absolute;
            height: 75%;
            width: 75%;
            background-color: transparent;
            border-radius: 50%; }

        &_light_theme {
            border: solid 1px $light_theme_border_color;
            box-shadow: 8px 8px 15px $light_theme_right_shadow, -8px -8px 15px $light_theme_left_shadow, inset 3px 3px 5px $light_theme_left_inside_shadow;
            background: linear-gradient(145deg, $light_theme_right_gradient, $light_theme_left_gradient);

            &::after {
                border: solid 1px $light_theme_border_color;
                box-shadow: 5px 5px 10px $light_theme_right_shadow; } }

        &_dark_theme {
            border: solid 1px $dark_theme_border_color;
            box-shadow: 8px 8px 15px $dark_theme_right_shadow, -8px -8px 15px $dark_theme_left_shadow, inset 3px 3px 5px $dark_theme_left_inside_shadow;
            background: linear-gradient(145deg, $dark_theme_right_gradient, $dark_theme_left_gradient);

            &::after {
                border: solid 1px $dark_theme_border_color;
                box-shadow: 5px 5px 10px $dark_theme_right_shadow; } }

        &_paused {
            color: $paused-color; } }

    &__controller {
        display: flex;
        align-items: center;
        justify-content: center; }

    &__button {
        cursor: pointer;
        margin: 30px;
        height: 75px;
        width: 75px;
        font-weight: bold;
        border-radius: 205px;
        background: linear-gradient(145deg, #ffffff, #e6e6e6);
        box-shadow: 8px 8px 15px #a8a8a8, -8px -8px 15px #ffffff;
        outline: none;
        border: none;
        user-select: none;
        -webkit-tap-highlight-color: transparent;
        transition: .3s ease-in-out box-shadow;

        &:hover {
            box-shadow: 4px 4px 8px #a8a8a8, -4px -4px 8px #ffffff; }

        &:focus {
            box-shadow: 2px 2px 4px #a8a8a8, -2px -2px 4px #ffffff; }

        &:disabled {
            cursor: initial;
            box-shadow: none;

            .icon {
                svg {
                    fill: $paused-color; } } } } }

.is_theme_changing {
    * {
        transition: none; } }
