@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
.icon{display:flex;justify-content:center;transition:.3s ease-in-out all}.icon_light_theme{fill:#26272c}.icon_dark_theme{fill:#e1e1e1}.icon svg{width:30px}

.base_button{cursor:pointer;margin:20px;height:75px;width:75px;font-weight:bold;border-radius:205px;background:linear-gradient(145deg, #fff, #e6e6e6);box-shadow:8px 8px 15px #a8a8a8, -8px -8px 15px #ffffff;outline:none;border:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;-webkit-tap-highlight-color:transparent;transition:.3s ease-in-out box-shadow}.base_button_light_theme{border:solid 1px #d7d7d7;box-shadow:8px 8px 15px rgba(94,104,121,0.671),-8px -8px 15px #fff;background:linear-gradient(145deg, #fff, #e6e6e6)}.base_button_light_theme:focus{box-shadow:4px 4px 8px rgba(94,104,121,0.671),-4px -4px 8px #fff}.base_button_dark_theme{border:solid 1px #2b2c31;box-shadow:8px 8px 15px #191919,-8px -8px 15px #323232;background:linear-gradient(145deg, #212227, #201f24)}.base_button_dark_theme:focus{box-shadow:4px 4px 8px #191919,-4px -4px 8px #323232}.base_button:disabled{cursor:auto;cursor:initial;box-shadow:none}.base_button:disabled .icon svg{fill:#646464}

.timer{height:100%;width:100%;display:flex;flex-direction:column;align-items:center;justify-content:space-around}.timer__header{font-size:32px}.timer__time{display:flex;justify-content:center;align-items:center;font-weight:bold;font-size:40px;height:300px;width:300px;padding:10px;border-radius:100%;position:relative;transition:.3s ease-in-out color}.timer__time::after{content:'';position:absolute;height:75%;width:75%;background-color:transparent;border-radius:50%}.timer__time_light_theme{border:solid 1px #d7d7d7;box-shadow:8px 8px 15px rgba(94,104,121,0.671),-8px -8px 15px #fff,inset 3px 3px 5px rgba(94,104,121,0.671);background:linear-gradient(145deg, #fff, #e6e6e6)}.timer__time_light_theme::after{border:solid 1px #d7d7d7;box-shadow:5px 5px 10px rgba(94,104,121,0.671)}.timer__time_dark_theme{border:solid 1px #2b2c31;box-shadow:8px 8px 15px #191919,-8px -8px 15px #323232,inset 3px 3px 5px #0c0d0f;background:linear-gradient(145deg, #212227, #201f24)}.timer__time_dark_theme::after{border:solid 1px #2b2c31;box-shadow:5px 5px 10px #191919}.timer__time_paused{color:#646464}.timer__controller{display:flex;align-items:center;justify-content:center}.timer__button{cursor:pointer;margin:30px;height:75px;width:75px;font-weight:bold;border-radius:205px;background:linear-gradient(145deg, #fff, #e6e6e6);box-shadow:8px 8px 15px #a8a8a8, -8px -8px 15px #ffffff;outline:none;border:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;-webkit-tap-highlight-color:transparent;transition:.3s ease-in-out box-shadow}.timer__button:hover{box-shadow:4px 4px 8px #a8a8a8, -4px -4px 8px #ffffff}.timer__button:focus{box-shadow:2px 2px 4px #a8a8a8, -2px -2px 4px #ffffff}.timer__button:disabled{cursor:auto;cursor:initial;box-shadow:none}.timer__button:disabled .icon svg{fill:#646464}.is_theme_changing *{transition:none}

.App{height:100%;display:flex;flex-direction:column;align-items:center;justify-content:center}.App_light_theme{background-color:#dcdcdc;color:#26272c;fill:#26272c}.App_dark_theme{background-color:#26272c;color:#e1e1e1;fill:#e1e1e1}

html, body, #root {
  height: 100%;
}

* {
  margin: 0;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
